@import url(https://fonts.googleapis.com/css2?family=Aclonica&family=Mali:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Aclonica",sans-serif}@keyframes hoverPop{0%,100%{transform:scale(1)}33%{transform:scale(1.05)}}@keyframes shine-header{0%{background-position:-100px}40%,100%{background-position:270px}}::-webkit-input-placeholder{color:#0b0b16 !important;font-size:14px !important;font-family:"Mali",cursive;padding-left:40px}:-ms-input-placeholder{color:#0b0b16 !important;font-size:14px !important;font-family:"Mali",cursive;padding-left:40px}::-ms-input-placeholder{color:#0b0b16 !important;font-size:14px !important;font-family:"Mali",cursive;padding-left:40px}::placeholder{color:#0b0b16 !important;font-size:14px !important;font-family:"Mali",cursive;padding-left:40px}input.form-control:focus{box-sizing:border-box;width:100%;text-indent:40px;font-family:"Mali",cursive;color:#0b0b16}p{font-family:"Mali",cursive}.t-center{text-align:center}.t-right{text-align:right}.t-12{font-size:12px}.t-14{font-size:14px}.t-white{color:#fefefe}.mb-0{margin-bottom:0px}.mb-2{margin-bottom:2rem}.mb-5{margin-bottom:5em}.p-20{padding:20px}pre{height:500px;overflow-y:scroll;font-family:"Mali",cursive !important}.br-12{border-radius:12px}.hero-div{background-image:linear-gradient(to right, #380036, #0CBABA);margin-bottom:50px;color:#fefefe;font-family:"Aclonica",sans-serif}input:focus{outline:none !important;box-shadow:none;-webkit-box-shadow:none !important}.d-flex{display:inline-flex}.mast{position:absolute;bottom:0;padding:10px;opacity:.9}.masonry-column{padding:0 3px !important}.masonry-grid>div .thumbnail{margin:5px 1px}.dark-white{color:#d6cdcd}.hero-text{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#fefefe;text-align:center}.hero-search{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#fefefe;text-align:center}.hero-search>input{box-shadow:0 -2px 10px #000}.search-icon{background-color:#fefefe;border:0px;position:absolute;top:5px;left:26px}.skeleton{background-color:#eee}.skeleton-image{width:100%;height:450px;background-image:linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 80px);animation:shine-header 1.6s infinite linear;background-size:300px}.skeleton-name{width:200px;height:20px;background-image:linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 80px);animation:shine-header 1.6s infinite linear;background-size:300px}.skeleton-location{width:200px;height:10px;background-image:linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 80px);animation:shine-header 1.6s infinite linear;background-size:300px}.my-modal{display:block;position:fixed;z-index:100;padding-top:100px;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,.4)}.my-modal-content{background-color:#fefefe;margin:auto;margin-bottom:50px;border:1px solid #888;width:80%;transition:.3s}.my-modal-content>img{border-radius:5px 5px 0 0}.object-fit{object-fit:cover}.home-link{padding:50px;text-align:right;cursor:pointer}.search{background-color:transparent;color:#d6cdcd;border:2px solid #e7e7e7;cursor:pointer;font-family:"Mali",cursive;padding:.4em;border-radius:8px}.search:hover{animation-name:hoverPop;animation-duration:.8s;outline:none !important;box-shadow:none;-webkit-box-shadow:none !important}.search:focus{outline:none !important;box-shadow:none;-webkit-box-shadow:none !important}.sendmsg{background-image:linear-gradient(to right, #380036, #0CBABA);color:#fefefe;cursor:pointer;font-family:"Mali",cursive;padding:.4em;border:2px solid #e7e7e7;border-radius:8px}.sendmsg:hover{animation-name:hoverPop;animation-duration:.8s;outline:none !important;box-shadow:none;-webkit-box-shadow:none !important}.sendmsg:focus{outline:none !important;box-shadow:none;-webkit-box-shadow:none !important}.close-modal{color:#fefefe;text-align:right;margin-right:30px;font-size:25px;opacity:.5;cursor:pointer}.gallery{position:absolute;top:40%;margin:auto;bottom:0;left:0;right:0}.center-sm{text-align:center}@media only screen and (min-width: 768px){.hero-text{left:30%}.center-sm{text-align:justify}}
